import React from 'react';

const ListItem = (props) => {
    return (
        <>
            <div className='container my-1'>
                <div className='row justify-content-center'>
                    <div className='col-lg-5 col-md-8 bg-light py-1'>
                    <div className='row'>
                            <div className='col-10'>
                                <p className='h5'>{props.put}</p>
                            </div>
                            <div className='col-2'>
                                <button type='button' className='btn' onClick={() => {props.del(props.id)}}><i class="fas fa-trash-alt"></i></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ListItem;
