import React, { useState } from 'react';
import ListItem from './ListItem';

const Todo = () => {
    const [input,setInput] = useState();

    const [addItem, setAdditem] = useState([]);

    const inputEvent = (event) => {
        const value = event.target.value;

        setInput(value);
    }

    const add = () => {
        setAdditem((preVal) => {
            return([
                ...preVal,
                input
            ])
        });

        setInput('');
        console.log(addItem);

    }

    const delt = (id) => {
        console.log('deleted');
        setAdditem((preVal) => {
            return preVal.filter((val, index) => {
                return index !== id;
            });
        });
        // addItem.filter((val, ind) => {
        //     // if(!ind){
        //     //     return[val]
        //     // }

        // })
    }
    return (
        <>
        {/* Heading */}
            <div className='continer-fluid my-5 bg-light'>
                <div className='row justify-content-center'>
                    <div className='col-4'>
                        <h1 className='text-center'>Todo App</h1>
                    </div>
                </div>
            </div> 

        {/* Add task area */}

            <div className='container my-3'>
                <div className='row justify-content-center '>
                    <div className='col-lg-5 col-md-8 bg-light px-2 py-3'>
                        <div className='row'>
                            <div className='col-10'>
                            <input 
                                type="text" 
                                class="form-control" 
                                placeholder="Add task" 
                                aria-label="First name" 
                                onChange={inputEvent}  
                                value={input}                              
                            />
                            </div>
                            <div className='col-2'>
                            <button type="button" class="btn btn-primary" onClick={add}>Add</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* todo list */}
            {
                addItem.map((val, ind) => {
                    return(
                        <ListItem 
                            key= {ind}
                            id={ind}
                            put = {val}
                            del={delt} 
                        />
                    )
                })
            }
            {/* <ListItem 
                put = {input}
            /> */}
        </>
    )
}

export default Todo;
